body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#editor {
    position: absolute;
    top: 0px;
    left: 0px;
    height: 100%;
    width: 100%;
}

#floor-planner{
    position: absolute;
    z-index: 1;
    left: 300px;
}

.points{
    position: absolute;
    bottom: 10px;
    right: 10px;
    z-index: 1;
}

#clear{
    position: absolute;
    top: 10px;
    right: 10px;
}

#create-floor{
    position: absolute;
    bottom: 10px;
    left: calc(50% - 46px);
}
